<template>
  <div id="safari" v-if="active">
    <header id="control-panel">
      <section id="control-buttons">
        <div id="close" class="button" v-on:click="close()">x</div>
        <div id="resize" class="button"></div>
        <div id="hide" class="button"></div>
      </section>
      <section id="search">{{pages[activeTab].url}}</section>
    </header>
    <section id="tabs">
      <div
        v-for="(page, index) in pages"
        v-bind:key="page.id"
        v-on:click="switchTab(index)"
        v-bind:class="['tab-header', { active: activeTab === index }]"
      >{{page.title}}</div>
      <div id="new-tab" class="tab-header active" @click="createTab('Favorites')">
        <div>+</div>
      </div>
    </section>
    <div class="tab">
      <keep-alive>
        <component :is="currentPage" :index="activeTab" :key="currentPage + activeTab" :url="pages[activeTab].url" class="page-container"></component>
      </keep-alive>
    </div>
  </div>
</template>

<script>
import FavoritesPage from "./components/FavoritesPage.vue";
import DonatePage from "./components/DonatePage.vue";
import CustomPage from "./components/CustomPage.vue";
import RulesPage from "./components/RulesPage.vue";
import Vue from 'vue';

var app = undefined;
export default {
  name: "App",
  data: function () {
    return {
      pages: [{id:"FavoritesPage0", title:"Favorites", url:"Favorites", type:'Favorites'}],
      activeTab: 0,
      active: true,
    };
  },
  methods: {
    createTab: function (name) {
      this.activeTab++;
      this.pages.push({id:`${name}Page${this.activeTab}`, title: name, url:name, type:name});
    },
    createCustomTab: function (url) {
      this.activeTab++;
      this.pages.push({id:`CustomPage${this.activeTab}`, title: 'Custom', url:url, type:'Custom'});
    },
    switchTab: function (id) {
      this.activeTab = id;
    },
    redirect: function (page) {
      this.$children[this.activeTab].$destroy();
      Vue.set(this.pages, this.activeTab, {id:`${page}Page${this.activeTab}`, title: page, url:page, type:page});
    },
    redirectToCustom: function (url) {
      this.$children[this.activeTab].$destroy();
      Vue.set(this.pages, this.activeTab, {id:`CustomPage${this.activeTab}`, title: 'Deposit', url:url, type:'Custom'});
    },
    openRule: function (rule){
      this.$children[this.activeTab].$destroy();
      Vue.set(this.pages, this.activeTab, {id:`RulesPage${this.activeTab}`, title: rule.title, url:rule.url, type:'Rules'});
    },
    callEvent: function (event) {
      console.log(event);
      //mp.trigger("safari", "close");
      //mp.trigger("donate", event);
    },
    close: function () {
      this.$children.forEach(element => {
        element.$destroy();
      });
      this.pages = [{id:"FavoritesPage0", title:"Favorites", url:"Favorites", type:'Favorites'}],
      this.activeTab = 0;
      //mp.trigger("safari", "close");
    },
    pageContains: function(page) {
      return this.pages.indexOf(page) > -1
    }
  },
  created: function(){
    app = this 
  },
  computed: {
    currentPage: function () {
      return this.pages[this.activeTab].type + "Page";
    },
  },
  components: {
    FavoritesPage,
    DonatePage,
    CustomPage,
    RulesPage,
  },
};
export {app}
</script>
<style lang="scss">
    h1,h2,h3,h4,h5,h6,p{
        margin:0;
    }
    button, input{
        outline: none;
    }
    ul{
        list-style: none;
        margin:0;
        padding: 0;
    }
</style>
<style scoped lang="scss">
#safari{
    position: absolute;
    top: 20px;
    left: 5vw;
    width: 90vw;
    height: 90vh;
    background: #f6f6f6;
    box-shadow: 0px 0px 10px rgba(0,0,0,0.3);
    border-radius: 4px;
    overflow: hidden;
    border: 1px solid rgb(0 0 0 / 20%);

    margin:0;
    font-family: 'Ubuntu', sans-serif;
    user-select: none;
}
#control-panel{
    background: #e0e0e0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
    height: 40px;
}
#control-buttons{
    display: flex;
}
#control-buttons .button{
    border-radius: 50%;
    height: 14px;
    width: 14px;
    margin: 0 3px;
    cursor: pointer;
    box-shadow: 0px 0 1px rgba(0,0,0,0.3);
    opacity: 0.85;
    text-align: center;
    line-height: 14px;
    font-size: 10px;
}
#control-buttons #close.button{
    background: #ff5d58;
    color: #000;
}
#control-buttons #resize.button{
    background: #fbbe03;
}
#control-buttons #hide.button{
    background: #10cd3d;
}
#control-panel #search{
    width: 40%;
    height: 60%;
    background: #f7f7f7;
    margin: 0 auto;
    border-radius: 4px;
    box-shadow: 0px 0 1px rgba(0,0,0,0.3);
    text-align: center;
    line-height: 150%;
    overflow: hidden;
}
#tabs{
    display: flex;
    text-align: center;
    height: 30px;
    line-height: 30px;
    background: #e0e0e0;
    box-shadow: 0px 1px 1px rgba(0,0,0,0.3);
}
#tabs .tab-header{
    flex-basis: 100%;
    cursor: pointer;
    border: 1px solid rgb(0 0 0 / 10%);
    border-right: none;
    overflow: hidden;
}
#tabs .tab-header.active{
    background: #d0d0d0;
}
#tabs #new-tab.tab-header{
    flex-basis: auto;
    overflow: visible;
}
#tabs #new-tab.tab-header div{
    width: 30px;
    color: rgba(0,0,0,0.3);
    font-size: 30px;
    text-shadow: none;
}
.tab{
    height: calc(100% - 70px);
}
.page-container{
    height: 100%;
    position: relative;
}

</style>
<style>
.modal{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0,0,0,0.8);
    color: #fff;
    z-index: 5;
}
.modal.hidden{
    display: none !important;
}
.modal .modal-body{
    background: #7b6ca9;
    padding: 20px;
    border-radius: 8px;
}
.modal .modal-form{
    display: flex;
    flex-direction: column;
}
.modal .modal-form .modal-input{
    border: none;
    padding: 0px 20px;
    border-radius: 10px;
    height: 45px;
    font-size: 17px;
    background: #414352;
    margin-top:7px;
    margin-left: 0px;
    box-shadow: 0 1px 5px 0 rgba(0,0,0,.5);
    color: #fff;
    outline: none;
}
.modal .modal-form label{
    margin-top:7px;
    margin-left: 7px;
}
.modal .modal-buttons{
    display: flex;
    justify-content: space-between;
    margin-top: 15px;
}
.modal .modal-form .buy{
    margin: 0 5px;
    flex-basis: 48%;
}

.modal-form.alert .buy{
    flex-basis: 100%;
}

.widget{
    margin: 10% auto 40px!important;
}
.widget-frame{
    height: calc(90vh - 70px)!important;
    width: 90vw!important;
    left: 5vw!important;
    top: calc(5vh + 70px)!important;
}
.vs__dropdown-toggle{
    border:none;
}
.v-select{
    color: #fff;
    margin-top: 7px;
    border: none;
    border-radius: 10px;
    font-size: 17px;
    background: #414352;
    margin-top: 7px;
    margin-left: 0px;
    box-shadow: 0 1px 5px 0 rgba(0,0,0,.5);
    color: #fff;
    outline: none;
}
.vs__dropdown-toggle{
    padding: 0;
    border: none;
}
.vs__selected{
    color: #fff;
    padding: 0 20px;
}
.vs__search, .vs__search:focus {
    color: #fff;
    height: 45px;
    line-height: 45px;
    margin:0;
}
.vs__clear, .vs__open-indicator{
    fill: rgba(255,255,255, 0.8);
}
.vs__dropdown-menu{
    background: #414352;
    border-radius:4px;
    top: calc(100% - -3px);

}
.vs__dropdown-option{
    color:#fff;
}
.vs__dropdown-option--highlight{
    background: #ffba41;
}
</style>