<template>
  <form class="modal-form">
        <label for="">Вы получите: {{amount * 1100}}$ за {{amount}} PCoins</label>
        <input class="modal-input" type="text" v-model="amount" placeholder="PCoins" @input="checkInput()">
        <div class="modal-buttons">
            <button class="buy" @click="send()">Конвертировать</button>
            <button class="buy" @click="closeModal()">Отмена</button>
        </div>
    </form>
</template>

<script>
export default {
    data: function () {
      return {
        amount: "",
      }
    },
    methods:{
        send: function(){
            this.closeModal();
            //mp.trigger("safari", "close");
            //mp.trigger("donateConvert", this.amount);
        },
        closeModal: function(){
            this.$parent.closeModal()
        },
        checkInput: function(){
          let reg = new RegExp('^[0-9]+$');
          if(!reg.test(this.amount)){
            this.amount = this.amount.slice(0, this.amount.length-1);
          }
        }
    },
}
</script>

