<template>
    <section id="password-form">
        <Logo></Logo>
        <input class="input" type="text" v-model="password" placeholder="Пароль">
        <button class="btn join" @click="join(id, password);">Присоединиться</button>
    </section>
</template>

<script>
import Logo from './Logo';
export default {
    props:{
        id:Number,
    },
    data: function() {
        return {
            password: "",
        };
    },
    methods: {
        join: function(id, password){
            this.password = ""
            this.$emit('join-by-pass', id, password);
      },
    },
    components:{
        Logo
    }
};
</script>

