<template>
    <div id="wrapper">
        <section id="main">
            <div id="logo">
              <img :src="require('./assets/store/logo.png')" alt="">
              <h2 class="title">Открыто</h2>
              <h2 class="title"><span class="color-red">24</span><span class="color-orange">/</span><span class="color-green">7</span></h2>
            </div>
            <div class="container">
              <div id="categories">
                <div class="category"  
                v-for="(category, index) in categories" 
                v-bind:key="category.name" 
                :class="{active: index == currentCat}" 
                @click="selectCat(index)">
                  <h3 class="title">
                  <img class="icon" :src="require(`./assets/store/icons/${category.name}.svg`)" alt="">
                  {{category.title}}
                  </h3>
                </div>
              </div>
              <div class="items"
                v-for="(category, index) in items" 
                v-bind:key="index">
                <div class="items-container" v-if="index == currentCat">
                  <div class="item" 
                    v-for="(item, id) in category" 
                    v-bind:key="item.title + item.currCount"
                  >
                    <h4 class="title">{{item.title}}</h4>
                    <img class="item-icon" :src="require(`./assets/store/items/${item.name}.png`)" alt="">
                    <h4 class="price">${{item.price.toFixed(2)}}</h4>
                    <div class="buyItem">
                      <h3 class="title">{{item.title}}</h3>
                      <div class="buy">Кол-во <span class="amount-btn" @click="changeCount(id, -1)">-</span><input type="number" class="amount" v-model="item.count" @input="calcPrice()"><span class="amount-btn" @click="changeCount(id, 1)">+</span></div>
                      <!-- <div class="buy" @click="addItem(id)"><img class="icon" :src="require('./assets/store/icons/basket.svg')" alt="" ><span>Добавить</span></div> -->
                      <h4 class="price">${{item.price.toFixed(2)}}</h4>
                    </div>
                  </div>
                </div>
              </div>
              <div class="row">
                  <h3>Сумма покупки:</h3>
                  <h3 class="col-2-2">Выберите тип оплаты:</h3>
              </div>
              <div class="row">
                  <h3 class="sum">${{currentPrice.toFixed(2)}}</h3>
                  <div class="buy" @click="buy(true)"><img class="icon" :src="require('./assets/money.svg')" alt="" >Наличными</div>
                  <div class="buy" @click="buy(false)"><img class="icon" :src="require('./assets/credit-card.svg')" alt="" ><span>По карте</span></div>
              </div>
            </div>
        </section>
    </div>
</template>

<script>
let store = null;
export default {
    data: function() {
        return {
          categories: [ 
            {name:"products", title: "Продукты"},
            {name:"drinks", title: "Напитки"},
            {name:"electronics", title: "Электроника"}, 
            {name:"fishing", title: "Рыбалка"}, 
            {name:"instruments", title: "Инструменты"}, 
          ],
          items: [
            [
              {name:"0", title:"Пицца", count: 0, price: 15},
              {name:"1", title:"Чипсы", count: 0, price: 1},
              {name:"13", title:"Сэндвич", count: 0, price: 2},
              {name:"14", title:"Бургер", count: 0, price: 10},
              {name:"15", title:"Хот-Дог", count: 0, price: 5},
            ],
            [
              {name:"16", title:"eCola", count: 0, price: 1},
              {name:"17", title:"Sprunk", count: 0, price: 1},
              {name:"18", title:"Вода", count: 0, price: 2},
            ],
            [
              {name:"3", title:"Сим-карта", count: 0, price: 1},
              {name:"2", title:"Бумбокс", count: 0, price: 100},
              {name:"9", title:"Фонарик", count: 0, price: 20},
            ],
            [
              {name:"4", title:"Удочка", count: 0, price: 50},
              {name:"5", title:"Спининг", count: 0, price: 100},
              {name:"6", title:"Карбоновый спининг", count: 0, price: 200},
              {name:"7", title:"Наживка", count: 0, price: 0.5},
            ],
            [
              {name:"8", title:"Монтировка", count: 0, price: 3},
              {name:"10", title:"Молоток", count: 0, price: 2},
              {name:"11", title:"Гаечный ключ", count: 0, price: 4},
              {name:"12", title:"Связка ключей", count: 0, price: 1},
            ],

          ],
          currentCat: 0,
          currentPrice: 0,
        };
    },
    methods: {
        loadItems: function(items){
          if (items) items = JSON.parse(items);
          else return;
          for (let i = 0; i < items.length; i++) {
            for (let j = 0; j < items[i].length; j++) {
              this.items[i][j].price = items[i][j];    
              this.items[i][j].count = 0;        
            }           
          }
          this.items.splice(0,0)
          this.currentPrice = 0;
        },
        selectCat: function(index){
          this.currentCat = index;
        },
        changeCount: function(id, count) {
          if(this.currentCat == 2 && id == 0 && this.items[2][0].count + count > 1) return;
          if(this.currentCat == 2 && id == 1 && this.items[2][1].count + count > 1) return;
          if(this.currentCat == 4 && id == 3 && this.items[4][3].count + count > 1) return;
          if(this.items[this.currentCat][id].count + count < 0) return;
          this.items[this.currentCat][id].count += count;
          this.currentPrice += count * this.items[this.currentCat][id].price;
        },
        calcPrice: function(){
          this.currentPrice = 0;
          if(this.items[2][0].count > 1) this.items[2][0].count = 1;
          if(this.items[2][1].count > 1) this.items[2][1].count = 1;
          if(this.items[4][3].count > 1) this.items[4][3].count = 1;
          for (let i = 0; i < this.items.length; i++) {
            for (let j = 0; j < this.items[i].length; j++) {
              const item = this.items[i][j];
              item.count = Number(item.count.toString().replace(/\D+/, ''));
              if(item.count < 0) item.count = 0;
              if(item.count > 0){
                this.currentPrice += item.count * item.price;
              }    
            } 
          }
        },
        buy: function(isCash) {
          let json = [];
          for (let i = 0; i < this.items.length; i++) {
            for (let j = 0; j < this.items[i].length; j++) {
              const item = this.items[i][j];
              if(item.count > 0){
                json.push([item.title, item.count])
              }    
            } 
          }
          console.log(`buy cash=${isCash}`)
          //mp.trigger("storeBuy", JSON.stringify(json), isCash);
        },
    },
    components: {
    },
    created: function() {
      store = this;
    },
};
export {store}
</script>
  
<style lang="scss" scoped>
$GREEN:#019f6e;
$GRAY:#969696;
$LIGHTGRAY:#ededed;
.color-red{
  color:#dd1c23;
}
.color-orange{
  color:#ff7e00;
}
.color-green{
  color:#019f6e;
}
#wrapper {
  font-family: "Myriad Pro", sans-serif;
}
#main {
    position: absolute;
    width: 1280px;
    background: #fafafa;
    padding: 40px 0 20px;
    border-radius: 20px;
    left:50%;
    transform: translateX(-50%);
    transform-origin: left top;
}
#logo{
  margin-bottom: 30px;
  height: 160px;
  background: #019f6e;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px 0 30px;
  margin-bottom: 30px;
  img{
    height: 100%;
  }
  .title{
    color:white;
    text-transform: uppercase;
    font-size: 115px;
    -webkit-text-stroke-width: 4px;
    -webkit-text-stroke-color: #fff;
  }
}
.container{
  padding: 0 30px;
  color:#969696;
  text-transform: uppercase;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 30px;
  .category{
    background-color: $LIGHTGRAY;
    border-radius: 10px;
    width: 100%;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    padding: 10px;
    box-sizing: border-box;
    cursor: pointer;
    &.active{
      background-color: $GREEN;
      color:#ffffff;
      .title .icon{
          width: 35px;
          margin-right: 10px;
          filter: invert(100%);
      }
    }
    .title{
      display: flex;
      align-items: center;
      justify-content: flex-start;
      font-size: 23px;
      .icon{
          width: 35px;
          margin-right: 10px;
          filter: invert(59%) sepia(12%) saturate(6%) hue-rotate(317deg) brightness(98%) contrast(91%);
      }
    }
  }
  .items{
    grid-column: 2 / span 3;
    grid-row: 1;
    .items-container{
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-gap: 10px;
    }
    .item{
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        border-radius: 10px;
        background-color: $LIGHTGRAY;
        padding: 15px 10px;
        .title, .price{
          text-align: center;
          color: black;
          margin: 15px 0;
        }
        .title{
          font-size: 20px;
        }
        .price{
          font-size: 30px;
        }
        .buyItem{
          position: absolute;
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;
          opacity: 0;
          box-sizing: border-box;
          width: 100%;
          height: 100%;
          background: $GREEN;
          border-radius: 10px;
          transition: ease 0.2s opacity;
          padding: 15px 10px;
          color: white;
          .icon{
              width: 35px;
              margin-right: 10px;
              filter: invert(59%) sepia(12%) saturate(6%) hue-rotate(317deg) brightness(98%) contrast(91%);
          }
          .title, .price{
            color: white;
          }
          .amount-btn{
            margin: 0 10px;
            font-size: 36px;
            cursor: pointer;
          }
          .amount{
            padding: 5px 8px 5px 5px;
            background: $GRAY;
            color: #fff;
            border-radius: 10px;
            border: none;
            outline: none;
            width: 60px;
            font-size: 26px;
            text-align: center;
            appearance: none;
            &::-webkit-outer-spin-button,
            &::-webkit-inner-spin-button {
              -webkit-appearance: none;
            }
          }
          .buy{
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: $LIGHTGRAY;
            border-radius: 10px;
            height: 60px;
            font-size: 26px;
            font-weight: 600;
            width: 100%;
            margin-bottom: 15px;
            color: #969696;
            cursor: pointer;
            &:last-child{
              grid-column: 2;
            }
            .icon{
              width: 35px;
              margin-right: 10px;
              filter: invert(59%) sepia(12%) saturate(6%) hue-rotate(317deg) brightness(98%) contrast(91%);
            }
          }
        }
        &:hover{
          .buyItem{
            opacity: 1;
          }
        }
      }
  }
  .row{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin-top: 10px;
    font-size: 20px;
    grid-column-gap: 10px;
    grid-column: 2 / span 3;
    .sum{
      color: #000;
      font-size: 36px;
    }
    .buy{
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: $LIGHTGRAY;
      border-radius: 10px;
      height: 60px;
      font-size: 26px;
      font-weight: 600;
      width: 100%;
      margin-bottom: 15px;
      color: $GRAY;
      transition: ease 0.2s all;
      cursor: pointer;
      &:hover{
        background-color: $GREEN;
        color: #fff;
        .icon{
          filter: invert(100%);
        }
      }
      .icon{
        width: 35px;
        margin-right: 10px;
        filter: invert(59%) sepia(12%) saturate(6%) hue-rotate(317deg) brightness(98%) contrast(91%);
      }
    }
  }
  .col-2-2{
    grid-column: 2 / span 2;     
  }
  h1,h2,h3,h4,h5,h6{
    margin:0;
  }
}
@media (max-width: 1920px) {
    #main {
        transform: scale(0.8) translateX(-50%);
    }
}
</style>
