<template>
    <div id="wrapper">
      <Main v-show="currentMenu == 'Main'"></Main>
      <CreateLobby v-show="currentMenu == 'CreateLobby'"></CreateLobby>
      <LobbyList v-show="currentMenu == 'LobbyList'" :lobbies="lobbies"></LobbyList>
      <PasswordForm v-show="currentMenu == 'PasswordForm'"></PasswordForm>
      <Stats v-show="currentMenu == 'Stats'" :kills="kills" :deaths="deaths"></Stats>
      <MatchStats v-show="currentMenu == 'MatchStats'" :matchStats="matchStats"></MatchStats>
      <Leaders v-show="currentMenu == 'Leaders'" :leaders="leaders"></Leaders>
    </div>
</template>

<script>
let arena = null;
import Main from './Main';
import CreateLobby from './CreateLobby';
import LobbyList from './LobbyList';
import PasswordForm from './PasswordForm';
import Stats from './Stats';
import MatchStats from './MatchStats';
import Leaders from './Leaders';

export default {
    data: function() {
        return {
          currentMenu: 'Main',
          lobby: -1,
          kills: 0,
          deaths: 0,
          lobbies: [
              { MembersCount: 2, MaxMembers: 2, Fee: 0, Password: '', Location: 0, Gun: 0, Started: false, CurrentSpawn: 0 },
              { MembersCount: 10, MaxMembers: 12, Fee: 1000, Password: '123', Location: 5, Gun: 2, Started: false, CurrentSpawn: 0 },
              { MembersCount: 11, MaxMembers: 12, Fee: 5000, Password: '', Location: 2, Gun: 8, Started: false, CurrentSpawn: 0 },
            ],
          matchStats: [],
          leaders: [
              ["Egor Andrushko", "3061", "2028", "1,5", "66", "111"],
              ["Kirill Tank", "2709", "4336", "0,6", "10", "192"],
              ["Maxim Pushkin", "2502", "1507", "1,7", "76", "85"],
              ["Anton Pavlov", "2259", "2780", "0,8", "53", "147"],
            ],
          locationsMaxPlayers: [
              16, 9, 16,
              2, 2, 4, 
              10, 4, 5,
              5, 7, 6,
              7, 4, 6
            ],
          locations: [
                "Порт", "Поезда", "Вилла",
                "Стройка", "Крыша", "Контейнеры",
                "Каменоломня", "Полигон", "Кубы",
                "Human Labs", "Деревня", "Завод",
                "Плавильня", "Мойка", "Остров"
            ],
          guns: ["Pistol","SMG","Rifle", "Shotgun", "Uzi", "Sniper", "Revolver", "Carbine","Axe"],
        };
    },
    methods: {
        update: function(lobbies, leaders){
            this.lobbies = [];
            if(lobbies && lobbies.length > 0){
                lobbies.forEach(lobby => {
                    this.lobbies.push(JSON.parse(lobby));
                });
            }
            this.leaders = [];
            if(leaders && leaders.length > 0){
                leaders.forEach(leader => {
                    this.leaders.push(leader);
                });
            }
        },
        showMatchStats: function(matchStats){
            this.matchStats = [];
            matchStats.forEach(stat => 
            {
                this.matchStats.push(stat);
            });
            this.currentMenu = 'MatchStats';
        },
    },
    created: function() {
      arena = this;
    },
    components:{
      Main,
      CreateLobby,
      LobbyList,
      PasswordForm,
      Stats,
      MatchStats,
      Leaders
    }
};
export {arena}
</script>

