<template>
  <div class="modal-body">
        <form class="modal-form">
            <h3 class="title">Стоимость: {{calcPrice()}} PCoins</h3>
            <div v-for="(job,i) in jobs" :key="i" class="list-item">
                <input class="inp-cbx" :id="i" type="checkbox" v-model="selected[i]"/>
                <label class="cbx" :for="i">
                    <span>
                        <svg width="12px" height="10px">
                            <use xlink:href="#check"></use>
                        </svg>
                    </span>
                    <span>{{job.name}}</span>
                    <svg class="inline-svg">
                        <symbol id="check" viewbox="0 0 12 10">
                            <polyline points="1.5 6 4.5 9 10.5 1"></polyline>
                        </symbol>
                    </svg>
                </label>
            </div>
                <div class="modal-buttons">
                    <button class="buy" @click="send()">Купить</button>
                    <button class="buy" @click="closeModal">Отмена</button>
                </div>
        </form>
    </div>
</template>

<script>
import {jobs} from '../../assets/jobs'
export default {
    data: function () {
      return {
        price: 100,
        selected: [false, false, false, false, false, false, false, false, false, false, false, false, false],
        jobs: jobs
      }
    },
    methods:{
        calcPrice: function(){
            let newPrice = 0;
            for (let index = 0; index < this.selected.length; index++) {
                if(this.selected[index]) newPrice += this.price;
            }
            return newPrice;
        },
        send: function(){
            this.closeModal();
            //mp.trigger("safari", "close");
            let jobs = [];
            for (let index = 0; index < this.selected.length; index++) {
              if(this.selected[index]) jobs.push(this.jobs[index].id);
              this.selected[index] = false;
            }
            //mp.trigger("donateJobs", JSON.stringify(jobs)); 
        },
        closeModal: function(){
            this.$parent.closeModal()
        },
    },
}
</script>

<style scoped>
.title{
    text-align: center;
    margin-bottom: 10px;
}
* {
  box-sizing: border-box;
}
.list-item{
    margin-bottom: 10px;
}
.cbx {
  -webkit-user-select: none;
  user-select: none;
  cursor: pointer;
  padding: 6px 8px;
  border-radius: 6px;
  overflow: hidden;
  transition: all 0.2s ease;
}
.cbx:not(:last-child) {
  margin-right: 6px;
}
.cbx:hover {
  background: rgba(0,119,255,0.06);
}
.cbx span {
  float: left;
  vertical-align: middle;
  transform: translate3d(0, 0, 0);
}
.cbx span:first-child {
  position: relative;
  width: 18px;
  height: 18px;
  border-radius: 4px;
  transform: scale(1);
  border: 1px solid #cccfdb;
  transition: all 0.2s ease;
  box-shadow: 0 1px 1px rgba(0,16,75,0.05);
  margin-right: 5px;
}
.cbx span:first-child svg {
  position: absolute;
  top: 3px;
  left: 2px;
  fill: none;
  stroke: #fff;
  stroke-width: 2;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-dasharray: 16px;
  stroke-dashoffset: 16px;
  transition: all 0.3s ease;
  transition-delay: 0.1s;
  transform: translate3d(0, 0, 0);
}
.cbx span:last-child {
  padding-left: 8px;
  line-height: 18px;
}
.cbx:hover span:first-child {
  border-color: #ffba41;
}
.inp-cbx {
  position: absolute;
  visibility: hidden;
}
.inp-cbx:checked + .cbx span:first-child {
  background: #ffba41;
  border-color: #ffba41;
  animation: wave 0.4s ease;
}
.inp-cbx:checked + .cbx span:first-child svg {
  stroke-dashoffset: 0;
}
body {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizelegibility;
  color: #223254;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.inline-svg {
  position: absolute;
  width: 0;
  height: 0;
  pointer-events: none;
  user-select: none;
}
@media screen and (max-width: 640px) {
  body {
    width: auto;
    display: inherit;
    padding: 20px;
  }
  .cbx {
    width: 100%;
    margin-bottom: 4px;
    display: inline-block;
  }
}
@-moz-keyframes wave {
  50% {
    transform: scale(0.9);
  }
}
@-webkit-keyframes wave {
  50% {
    transform: scale(0.9);
  }
}
@-o-keyframes wave {
  50% {
    transform: scale(0.9);
  }
}
@keyframes wave {
  50% {
    transform: scale(0.9);
  }
}

</style>
