import Vue from 'vue'
import VueRouter from 'vue-router'
import Safari from '../apps/safari/App.vue'
import Questionnaire from '../apps/drivingSchool/App.vue'
import Store from '../apps/store/Store.vue'
import Arena from '../apps/arena/Arena.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Safari',
    component: Safari
  },
  {
    path: '/questionnaire',
    name: 'Questionnaire',
    component: Questionnaire
  },
  {
    path: '/shop',
    name: 'Shop',
    component: Store
  },
  {
    path: '/arena',
    name: 'Arena',
    component: Arena
  },
  // {
  //   path: '/about',
  //   name: 'About',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // }
]

const router = new VueRouter({
  routes
})

export default router
