<template>
  <form class="modal-form">
        <label for="">Новое имя: {{name}}</label>
        <input class="modal-input" type="text" v-model="name" placeholder="Имя">
        <label for="">Новая фамилия: {{surname}}</label>
        <input class="modal-input" type="text" v-model="surname" placeholder="Фамилия">
        <div class="modal-buttons">
            <button class="buy" @click="send()">Сменить</button>
            <button class="buy" @click="closeModal()">Отмена</button>
        </div>
    </form>
</template>

<script>
export default {
    data: function(){
        return {
            name: "",
            surname: ""
        }
    },
    methods:{
        send: function(){
            this.closeModal();
            //mp.trigger("safari", "close");
            //mp.trigger("donateName", this.name, this.surname);
        },
        closeModal: function(){
            this.$parent.closeModal()
        }
    }
}
</script>

