<template>
  <div class="modal-body">
        <form class="modal-form" v-show="vehicles.length > 0">
                <label for="">Стоимость: {{cost}} PCoins</label>
                <label for="">Новый 6-значный номер:</label>
                <input class="modal-input" type="text" v-model="newNumber" placeholder="Номер" v-on:input="validate(); getCost();">
                <label for="" v-if="selected != ''">Вы меняете: {{selected}} на {{newNumber}}</label>
                <label for="" v-if="selected == ''">Выберите номер для замены</label>
                <VueSelect :options="vehicles" placeholder="Выберите номер" v-model="selected" :reduce="vehicle => vehicle.number" label="label"></VueSelect>
                <div class="modal-buttons">
                    <button class="buy" @click="send()">Купить</button>
                    <button class="buy" @click="closeModal">Отмена</button>
                </div>
        </form>
            <form class="modal-form alert" v-show="vehicles.length == 0">
                <label for="">У вас нет авто!</label>
                <div class="modal-buttons">
                    <button class="buy" @click="closeModal">Закрыть</button>
                </div>  
            </form>
    </div>
</template>

<script>
import VueSelect from 'vue-select'
function howManyRepeated(str){
    const result = [];
    const strArr = str.toLowerCase().split("").sort().join("").match(/(.)\1+/g);
    
    if (strArr != null) {
      strArr.forEach((elem) => {
        result.push(elem[0]);
      });
    }
    return result;
  }
export default {
    props:{
        numbers: Array,
        models: Array
    },
    data: function () {
      return {
        selected: "",
        newNumber: "",
        cost: 40,
      }
    },
    methods:{
        send: function(){
            this.closeModal();
            //mp.trigger("safari", "close");
            //mp.trigger("donateNumber", this.selected, this.newNumber, this.cost); 
        },
        closeModal: function(){
            this.$parent.closeModal()
        },
        validate: function(){
            this.newNumber = this.newNumber.toUpperCase();
            this.newNumber = this.newNumber.replace(/[^A-Z^0-9]/g, '');
        },
        getCost: function(){
            if(this.newNumber.length > 6){
                this.newNumber = this.newNumber.slice(0, 6);
            }
            let repeatedArr = howManyRepeated(this.newNumber);
            this.cost = 40;
            repeatedArr.forEach(element =>{ 
                this.cost += (this.newNumber.toLowerCase().split(element).length - 2) * 10
            });
        }
    },
    computed:{
        vehicles: function(){
            if(this.numbers.length == this.models.length){
                let vehicles = [];
                for (let index = 0; index < this.numbers.length; index++) {
                    vehicles.push({"number":this.numbers[index], "label": `${this.models[index]} [${this.numbers[index]}]`});
                }
                return vehicles;
            }
            else return [];
        }
    },
    components:{
        VueSelect
    }
}
</script>

