<template>
    <section id="create-lobby">
        <Logo></Logo>
        <Back @back="back"/>
        <input class="input password" type="text" v-model="password" placeholder="Пароль">
        <p class="description">При вводе пароля, лобби меняет статус на "Закрытое"</p>
        <h2 class="block-title">Кол-во участников</h2>
        <div class="members">
          <VueSlider class="slider" :disabled="sliderDisabled" v-model="maxMembers" :min="2" :max="$parent.locationsMaxPlayers[location]" :tooltip="'none'" :contained="true" :width="'80%'"></VueSlider>
          {{maxMembers}}
        </div>
        <p class="description">Максимальное количество участников - {{$parent.locationsMaxPlayers[location]}}</p>
        <h2 class="block-title">Выбор локации</h2>
        <div class="row">
          <button class="btn" @click="selectLocation(0)" :class="{'selected': location == 0}">{{$parent.locations[0]}}</button>
          <button class="btn" @click="selectLocation(1)" :class="{'selected': location == 1}">{{$parent.locations[1]}}</button>
          <button class="btn" @click="selectLocation(2)" :class="{'selected': location == 2}">{{$parent.locations[2]}}</button>
        </div>
        <div class="row">
          <button class="btn" @click="selectLocation(3)" :class="{'selected': location == 3}">{{$parent.locations[3]}}</button>
          <button class="btn" @click="selectLocation(4)" :class="{'selected': location == 4}">{{$parent.locations[4]}}</button>
          <button class="btn" @click="selectLocation(5)" :class="{'selected': location == 5}">{{$parent.locations[5]}}</button>
        </div>
        <div class="row">
          <button class="btn" @click="selectLocation(6)" :class="{'selected': location == 6}">{{$parent.locations[6]}}</button>
          <button class="btn" @click="selectLocation(7)" :class="{'selected': location == 7}">{{$parent.locations[7]}}</button>
          <button class="btn" @click="selectLocation(8)" :class="{'selected': location == 8}">{{$parent.locations[8]}}</button>
        </div>
        <div class="row">
          <button class="btn" @click="selectLocation(9)" :class="{'selected': location == 9}">{{$parent.locations[9]}}</button>
          <button class="btn" @click="selectLocation(10)" :class="{'selected': location == 10}">{{$parent.locations[10]}}</button>
          <button class="btn" @click="selectLocation(11)" :class="{'selected': location == 11}">{{$parent.locations[11]}}</button>
        </div>
        <div class="row">
          <button class="btn" @click="selectLocation(12)" :class="{'selected': location == 12}">{{$parent.locations[12]}}</button>
          <button class="btn" @click="selectLocation(13)" :class="{'selected': location == 13}">{{$parent.locations[13]}}</button>
          <button class="btn" @click="selectLocation(14)" :class="{'selected': location == 14}">{{$parent.locations[14]}}</button>
        </div>
        <h2 class="block-title">Выбор оружия</h2>
        <div class="row">
          <button class="btn" @click="selectGun(0)" :class="{'selected': gun == 0}">{{$parent.guns[0]}}</button>
          <button class="btn" @click="selectGun(1)" :class="{'selected': gun == 1}">{{$parent.guns[1]}}</button>
          <button class="btn" @click="selectGun(2)" :class="{'selected': gun == 2}">{{$parent.guns[2]}}</button>
        </div>
        <div class="row">
          <button class="btn" @click="selectGun(3)" :class="{'selected': gun == 3}">{{$parent.guns[3]}}</button>
          <button class="btn" @click="selectGun(4)" :class="{'selected': gun == 4}">{{$parent.guns[4]}}</button>
          <button class="btn" @click="selectGun(5)" :class="{'selected': gun == 5}">{{$parent.guns[5]}}</button>
        </div>
        <div class="row">
          <button class="btn" @click="selectGun(6)" :class="{'selected': gun == 6}">{{$parent.guns[6]}}</button>
          <button class="btn" @click="selectGun(7)" :class="{'selected': gun == 7}">{{$parent.guns[7]}}</button>
          <button class="btn" @click="selectGun(8)" :class="{'selected': gun == 8}">{{$parent.guns[8]}}</button>
        </div>
        <h2 class="block-title">Первоначальный взнос</h2>
        <input class="input" type="text" v-model="fee" placeholder="Введите сумму">
        <p class="description">Максимальный взнос - 10.000$</p>
        <button class="btn create" @click="create()">Создать</button>
    </section>
</template>

<script>
import Logo from './Logo';
import VueSlider from 'vue-slider-component'
import 'vue-slider-component/theme/default.css'
import Back from './Back'
export default {
    data: function() {
        return {
            password: "",
            location: 0,
            gun: 0,
            fee: "",
            maxMembers: 2,
            sliderDisabled: false,
        };
    },
    methods: {
        selectLocation: function(location){
            this.location = location;
            if(this.$parent.locationsMaxPlayers[location] == 2){
              this.sliderDisabled = true;
            }
            else {
              this.sliderDisabled = false;
            }
            if(this.maxMembers > this.$parent.locationsMaxPlayers[location]){
              this.maxMembers = this.$parent.locationsMaxPlayers[location];
            }
        },
        back(){
          this.$parent.currentMenu='Main';
        },
        selectGun: function(gun){
            this.gun = gun;
        },
        create: function(){
            if(this.fee != "" && this.fee > 10000){
              alert('Максимальная сумма взноса 10.000$!');
              //mp.trigger('notify', 1, 9, 'Максимальная сумма взноса 10.000$!', 3000);
              return;
            } 
            this.$parent.currentMenu='LobbyList';
            if(this.fee == "") this.fee = 0;
            this.$parent.lobby = this.$parent.lobbies.length;
            this.$parent.lobbies.push({ MembersCount: 1, MaxMembers: this.maxMembers, Fee: this.fee, Password: this.password, Location: this.location, Gun: this.gun, Started: false, CurrentSpawn: 0 });
            //mp.trigger("Arena.CreateLobby", this.maxMembers, this.location, this.gun, this.fee, this.password);
        }
    },
    components:{
        VueSlider,
        Logo,
        Back
    }
};
</script>

<style lang="scss" scoped>
.vue-slider ::v-deep .vue-slider-process {
  background-color:#88B1ED;
}
.vue-slider ::v-deep .vue-slider-dot-handle {
  background-color: #88B1ED;
}
.vue-slider ::v-deep .vue-slider-dot {
  &-handle {
    background-color:#88B1ED;
    &-focus {
      box-shadow: 0px 0px 1px 2px rgba( #88B1ED, 0.36);
    }
  }
}
.btn{
  background:none;
  border:none;
  outline: none;
}
.row{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 10px;
  margin-bottom: 10px;
}
.block-title{
  color: rgba(255, 255, 255, 0.85);
  font-size: 16px;
  font-family: "ExoSoft";
  font-style: normal;
  font-weight: 500;
  margin-top: 10px;
  margin-bottom: 0.8em;
  text-align: left;
}
.description{
  color: rgba(255, 255, 255, 0.55);
  font-size: 1em;
  margin: 0;
  text-align: left;
}
.members{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 50%;
  color: #AF8CE7;
  font-size: 1.7em;
  line-height: 1.7em;
}
#create-lobby{
  position: absolute;
  left: 50%;
  top: 5%;
  transform: translate(-50%);
  background: linear-gradient(145.59deg, rgba(48, 0, 125, 0.8) 0%, rgba(0, 0, 0, 0.8) 54.22%);
  border-radius: 10px;
  width: 28vw;
  display: grid;
  grid-template-columns: 1fr;
  box-sizing: border-box;
  padding: 70px 35px 25px;
  font-family: "ExoSoft";
  font-style: normal;
  font-weight: normal;
    .btn{
      font-size: 1.1em;
      line-height: 1.1em;
      border-radius: 10px;
      padding: 10px 20px;
      background: rgba(242, 242, 242, 0.01);
      border: 1px solid rgba(222, 222, 222, 0.25);
      box-sizing: border-box;
      border-radius: 5px;
      color: rgba(255, 255, 255, 0.8);
      font-family: "ExoSoft";
      font-style: normal;
      font-weight: 300;
      &.selected{
        padding-bottom: 9px;
        border-bottom: 2px solid #F02A43;
      }
      &.create{
        background:transparent;
        position: relative;
        font-size: 2.1em;
        width: 35%;
        padding: 10px 20px;
        color: rgba(255, 255, 255, 0.8);
        margin:15px auto 0;
        font-family: "ExoSoft";
        font-style: normal;
        font-weight: bold;
        background: rgba(196, 196, 196, 0.01);
        border: 1px solid rgba(99, 105, 255, 0.75);
      }
  }
  .input{
    font-size: 1.3em;
    line-height: 1.3em;
    background: rgba(196, 196, 196, 0.01);
    border: 1px solid rgba(99, 105, 255, 0.75);
    width: 45%;
    padding: 10px 20px;
    border-radius: 5px;
    color: rgba(255, 255, 255, 0.8);
    outline: none;
    margin-bottom: 15px;
    box-sizing: border-box;
  }
}
</style>
