<template>
    <section id="stats" :style="position">
    <svg viewBox="0 0 120 120" version="1.1" xmlns="http://www.w3.org/2000/svg" class="circle">
        <defs>
            <linearGradient id="gradient" x1="0%" y1="50%" x2="100%" y2="100%">
                <stop offset="0" stop-color="#6C26FF"/>
                <stop offset="1" stop-color="#F02A43"/>
            </linearGradient>
        </defs>
        <circle cx="60" cy="60" r="50" fill="none" stroke="rgba(0, 0, 0, 0.2)" stroke-width="5px" transform="scale(1, -1) translate(0, -120) rotate(90 60 60)"/>
        <circle cx="60" cy="60" r="50" fill="none" stroke="url(#gradient)" stroke-width="5px" stroke-dasharray="313.6517333984375" :stroke-dashoffset="dashOffset" class="meter" transform="scale(1, -1) translate(0, -120) rotate(90 60 60)"/>
    </svg>
    <div class="time">{{Math.floor(time / 60 / 10)}}{{Math.floor(time / 60 % 10)}}:{{Math.floor(time % 60 / 10)}}{{Math.floor(time % 60 % 10)}}</div>
    <div>
        <p class="stats-block">Убийства: {{kills}}</p>
        <p class="stats-block">Смерти: {{deaths}}</p>
    </div>
    </section>
</template>

<script>
let arenaStats = null;
export default {
    props:{
        kills:Number,
        deaths:Number,
    },
    data: function() {
        return {
            time: 0,
            timeFrom: 10,
            timer: null,
            dashOffset: 0,
            position:{
                left: `calc(16% + 15px)`,
                bottom: `3%`
            }
        };
    },
    methods: {
        startTimer: function(time){
            this.timeFrom = time;
            this.stopTimer();
            this.time = time;
            this.timer = setInterval(()=>{
                if(this.time > 0) this.time -= 1;
                else this.stopTimer();
                let length = 313.6517333984375;
                let value = length;
                if(this.time > 0)
                    value = length - length * this.time / this.timeFrom;
                this.dashOffset = Math.max(0, value);
            }, 1000);
        },
        stopTimer: function(){
            if(this.timer != null) clearInterval(this.timer);
            this.time = 0;
            this.dashOffset=0;
        },
        updatePosition: function(left, bottom){
            this.position = {
                left:`calc(${left}% + 15px)`,
                bottom:`${bottom}%`,
            }
        }
    },
    created: function() {
      arenaStats = this;
    },
};
export {arenaStats}
</script>

<style lang="scss" scoped>
.btn{
  background:none;
  border:none;
  outline: none;
}
.time{
    grid-column: 1;
    grid-row: 1;
    text-align: center;
    font-size: 2rem;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: bold;
    width: 8rem;
}
.circle{
    grid-column: 1;
    grid-row: 1;
    width: 8rem;
}
#stats{
    position: absolute;
    bottom: 1.7vh;
    left: 16vw;
    background: rgba(21, 21, 21, 0.75);
    border-radius: 5px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    box-sizing: border-box;
    padding: 15px 10px;
    font-size: 1.5rem;
    line-height: 1.5rem;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: bold;
    color: #fff;
}
.meter{
    transition: stroke-dashoffset 500ms ease-in-out;
}
.stats-block{
    margin:0;
    &:first-child{
        margin-bottom: 5px;
    }
}
</style>
