<template>
  <div class="modal-body">
        <form class="modal-form">
                <label for="">Стоимость: {{cost}} PCoins</label>
                <label for="">Новый номер от 3 до 8 символов:</label>
                <input class="modal-input" type="text" v-model="newNumber" placeholder="Номер" v-on:input="getCost()">
                <div class="modal-buttons">
                    <button class="buy" @click="send()">Купить</button>
                    <button class="buy" @click="closeModal">Отмена</button>
                </div>
        </form>
    </div>
</template>

<script>
function howManyRepeated(str){
    const result = [];
    const strArr = str.toLowerCase().split("").sort().join("").match(/(.)\1+/g);
    
    if (strArr != null) {
      strArr.forEach((elem) => {
        result.push(elem[0]);
      });
    }
    return result;
  }
export default {
    props:{
        numbers: Array,
        models: Array
    },
    data: function () {
      return {
        selected: "",
        newNumber: "",
        cost: 80,
      }
    },
    methods:{
        send: function(){
            this.closeModal();
            //mp.trigger("safari", "close");
            //mp.trigger("donatePhone", this.newNumber); 
        },
        closeModal: function(){
            this.$parent.closeModal()
        },
        getCost: function(){
            if(this.newNumber.length > 8){
                this.newNumber = this.newNumber.slice(0, 8);
            }
            let repeatedArr = howManyRepeated(this.newNumber);
            this.cost = 80;
            repeatedArr.forEach(element =>{ 
                this.cost += (this.newNumber.toLowerCase().split(element).length - 2) * 10
            });
        }
    },
}
</script>

