<template>
  <div>
    <section id="lobby-list" v-show="!enteringPassword">
        <Logo></Logo>
        <div v-show="!lobbies.find(lobby => !lobby.Started)" class="no-lobby">
          Нет доступных лобби
        </div>
        <div v-for="(lobby, id) in lobbies" :key="id" class="lobby" v-show="!lobby.Started">
            <div class="name">Лобби {{id+1}}</div>
            <div class="password opened" v-if="lobby.Password == ''">Открытое</div>
            <div class="password closed" v-if="lobby.Password != ''">Закрытое</div>
            <div class="devider"></div>
            <div class="info-block">
              <div>Участники: {{lobby.MembersCount}}/{{lobby.MaxMembers}}</div>
              <div>Взнос: {{lobby.Fee}}$</div>
            </div>
            <div class="info-block">
              <div>Локация: {{$parent.locations[lobby.Location]}}</div>
              <div>Оружие: {{$parent.guns[lobby.Gun]}}</div>
            </div>
            <img class="icon leave" :src="require('./assets/icons/close-button.svg')" alt="" v-if="$parent.lobby == id" @click="leave(id)">
            <img class="icon" :src="require('./assets/icons/opened.svg')" alt="" v-else-if="lobby.Password == '' && lobby.MembersCount < lobby.MaxMembers" @click="join(id)">
            <img class="icon" :src="require('./assets/icons/closed.svg')" alt="" v-else-if="lobby.Password != '' || lobby.MembersCount == lobby.MaxMembers" @click="join(id)">
        </div>
        <div class="row">
            <a @click="leave()" class="back">Назад</a>
        </div>
    </section>
    <PasswordForm v-show="enteringPassword" :id="passwordFor" @join-by-pass="joinByPass"></PasswordForm>
  </div>
</template>
<script>
import PasswordForm from './PasswordForm';
import Logo from './Logo';
export default {
    props:{
        lobbies: Array,
    },
    data: function() {
        return {
          enteringPassword: false,
          passwordFor: -1,
        };
    },
    methods: {
      join: function(id){
        if(this.lobbies[id].MembersCount >= this.lobbies[id].MaxMembers)
        {
          alert('В лобби нет мест!');
          //mp.trigger('notify', 1, 9, 'В лобби нет мест!', 3000);
          return;
        }
        if(this.lobbies[id].Password != "")
        {
          this.passwordFor = id;
          this.enteringPassword = true;
          return;
        }
        //mp.trigger('Arena.Join', id);
        this.$parent.lobby = id;
        this.$parent.lobbies[id].MembersCount++;
      },
      joinByPass: function(id, password){
        this.enteringPassword = false;
        this.$parent.currentMenu='LobbyList';
        this.passwordFor = -1;
        if(this.lobbies[id].Password != password){
          alert('Неверный пароль!');
          //mp.trigger('notify', 1, 9, 'Неверный пароль!', 3000);
          return;
        } 
        //mp.trigger('Arena.Join', id);
        this.$parent.lobby = id;
        this.$parent.lobbies[id].MembersCount++;
      },
      leave: function(id){
        //mp.trigger('Arena.Leave');
        if(id){
          if(this.$parent.lobbies[id].MembersCount == 1){
            this.$parent.lobbies.splice(id, 1);
          }
          this.$parent.lobby = -1;
          this.$parent.lobbies[id].MembersCount--;
        }
        else{
          this.$parent.currentMenu='Main';
        }
      }
    },
    components:{
      PasswordForm,
      Logo
    }
};
</script>

<style lang="scss" scoped>
.back{
    position: absolute;
    right: 20px;
    top: 10px;
    cursor: pointer;
}
.btn{
  background:none;
  border:none;
  outline: none;
}
.icon{
  //width: 2.85rem;
  cursor: pointer;
  &.leave{
    filter: invert(0.4);
    width: 40px;
    padding: 0 5px;
  }
}

.lobby{
  width: 100%;
  background: rgba(0, 0, 0, 0.4);
  border-radius: 5px; 
  padding: 15px 25px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  box-sizing: border-box;
  font-family: "ExoSoft";
  font-style: normal;
  font-weight: 300;
  .info-block{
    font-size: 1.2rem;
    min-width: 160px;
    &:nth-child(5){
      min-width: 200px;
    }
  }
}
.no-lobby{
  font-size: 2.5rem;
  line-height: 2.5rem;
  text-align: center;
}
.devider{
  height: 2.5rem;
  border-right: 1px solid rgba(100, 100, 100, 0.6);
}
.password{
  padding: 5px 10px;
  border-radius: 5px;
  &.opened{
    border: 1px solid rgba(170, 247, 94);
    color: rgba(170, 247, 94);
  }
  &.closed{
    border: 1px solid rgba(247, 94, 94);
    color: rgba(247, 94, 94);
  }
}
.name{
  min-width: 100px;
  font-size: 1.7rem;
  line-height: 1.7rem;
  margin:0;
}
#lobby-list{
  position: absolute;
  left: 50%;
  top: 10%;
  transform: translateX(-50%);
  border-radius: 10px;
  width: 40vw;
  display: grid;
  grid-template-columns: 1fr;
  box-sizing: border-box;
  padding: 70px 35px 60px;
  background: linear-gradient(145.59deg, rgba(48, 0, 125, 0.8) 0%, rgba(0, 0, 0, 0.8)  95%);
  border-radius: 10px;
  font-family: "ExoSoft";
  font-style: normal;
  font-weight: 300;
  color:#fff;
    .btn{
      font-size: 1.7rem;
      line-height: 1.7rem;
      border-radius: 10px;
      padding: 30px 90px;
      font-family: "ExoSoft";
      font-style: normal;
      font-weight: 300;
    &.join{
      background: rgba(241, 42, 67, 0.05);
      border: 2px solid rgba(242, 43, 68, 0.5);
    }
    &.create{
      border: 2px solid rgba(159, 99, 236, 0.5);
      background: linear-gradient(90deg, rgba(159, 99, 236, 0.05) 0%, rgba(159, 99, 236, 0) 100%);
    }
  }
}
</style>
