<template>
    <form class="question">
      <div class="title">{{question['title']}}</div>
      <Answer v-for="(answer, index) in question['answers']" :key="id +''+index" :id="index" :questId="id" :text="answer" @select-answer="selectAnswer"></Answer>
    </form>
</template>

<script>
import Answer from './Answer.vue'
export default {
  props:{
    id: Number,
    question: Object,
  },
  data: function () {
    return {
    };
  },
  methods: {
    selectAnswer: function (a) {
      this.$emit('select-answer', this.id, a)
    },
  },
  components:{
    Answer
  }
};
</script>

