<template>
  <div id="questionnaire" v-if="active">
    <div class="row">
      <Question
        v-for="(item, index) in questList"
        :key="index"
        :id="index"
        :question="item"
        @select-answer="selectAnswer"
      ></Question>
      <div @click="confirmAnswer()" class="confirm">Подтвердить ответ</div>
    </div>
  </div>
</template>

<script>
import Question from "./components/Question.vue";
import { questions } from "./assets/questions.js";
var app = undefined;
export default {
  name: "App",
  data: function() {
    return {
      active: true,
      questList: [],
      userAnswers: [],
      questCount: 5,
    };
  },
  methods: {
    randomQuestions: function() {
      this.questList = [];
      let quests = questions.slice();
      for (let index = 0; index < this.questCount; index++) {
        let questId = Math.floor(Math.random() * quests.length);
        this.questList.push(quests[questId]);
        quests.splice(questId, 1);
      }
    },
    selectAnswer: function (quest, ans) {
      this.userAnswers[quest] = ans
    },
    confirmAnswer: function () {
      if(this.userAnswers.length < 5){
        for (let index = 0; index < this.userAnswers.length; index++) {
          const element = this.userAnswers[index];
          if(isNaN(element))
          {
            var quest = document.getElementsByClassName("question")[index];
            quest.scrollIntoView();
            return
          }
        }
        return
      }
      for (let index = 0; index < this.userAnswers.length; index++) {
        if(this.userAnswers[index] != this.questList[index]['correctAnswer']){
          //mp.trigger("drivingSchool_endTest", false)
          alert("FAILED");
          return
        }
      }
      //mp.trigger("drivingSchool_endTest", true)
      alert("PASSED");
    },
  },
  created: function() {
    app = this;
    this.randomQuestions();
  },
  components: {
    Question,
  },
};
export { app };
</script>

